:root {
  --w100: 100%;
  --w90: 90%;
  --w80: 80%;
  --height-nav: 80px;
  --app-theme: #f2ff00;
  --text-small: #1f1f1f;
  --heading: #113e66;
  --font-text: 1rem;
  --font-heading: 3rem;
  --font-subtext: 2rem;
  --displayFlex: flex;
  --flex-directcol: column;
  --flex-directrow: row;
}

.Nav-container {
  width: var(--w100);
  height: var(--height-nav);
  /* display: var(--displayFlex); */
}
.Nav-container .Nav-limit-container {
  width: var(--w80);
}
.Nav-container .Nav-limit-container .Nav-wrapper {
  width: var(--w100);
  display: var(--displayFlex);
  flex-direction: var(--flex-directrow);
  justify-content: space-between;
  background-color: var(--app-theme);
}
